import VideoDialog from '@/components/dialog/VideoDialog';
export default {
  name: 'videos',
  components: {
    VideoDialog: VideoDialog
  },
  data: function data() {
    return {
      activeIndex: 1,
      showMore: false,
      video: null,
      poster: this.$useWebpImg('@/assets/img/mien/videos/cover.jpg'),
      showVideoDialog: false
    };
  },
  methods: {
    openVideoDialog: function openVideoDialog(index) {
      this.activeIndex = index;
      this.video = index < 9 ? "360p_saoerke".concat(index, ".mp4") : "360p_sek".concat(index, ".mp4");
      this.showVideoDialog = true;
    }
  }
};